@import url(https://fonts.googleapis.com/css2?family=Archivo+Black&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: rgb(187, 187, 187);
}

.screen {
  -webkit-align-items: center;
          align-items: center;
  background-color: rgb(66, 66, 66);
  border-radius: 5px;
  display: -webkit-flex;
  display: flex;
  font-size: 30px;
  -webkit-flex-direction: column;
          flex-direction: column;
  height: 20%;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  margin: 30px 0;
  padding: 20px;
  width: 90%;
}

.screen .up {
  color: white;
  font-size: 35px;
  height: 50%;
  width: 100%;
}
.screen .down {
  color: rgb(145, 145, 145);
  font-size: 25px;
  height: 50%;
  text-align: end;
  width: 100%;
}
.calc-body {
  -webkit-align-items: center;
          align-items: center;
  background-color: rgb(206, 206, 206);
  border-radius: 10px;
  box-shadow: 5px 5px 10px rgb(133, 133, 133);
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  height: 560px;
  margin: 5px;
  min-width: 350px;
}
.button-container {
  width: 90%;
  height: 60%;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.btn {
  background-image: linear-gradient(white, rgb(170, 170, 170));
  border: none;
  border-radius: 10px;
  cursor: pointer;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 20px;
  font-weight: 600;
  height: 60px;
  width: 22%;
}

.btn:first-child {
  color: red;
  font-size: 25px;
}

.btn:last-child {
  background-image: linear-gradient(rgb(96, 95, 202), rgb(28, 34, 107));
  color: white;
  width: 47%;
}

.btn:active {
  background-color: red;
}
.app {
  -webkit-align-items: center;
          align-items: center;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  font-family: 'Archivo Black', sans-serif;
  height: 100vh;
  -webkit-justify-content: center;
          justify-content: center;
  width: 100vw;
}
.hist-body {
  -webkit-align-items: center;
          align-items: center;
  background-color: rgb(80, 80, 80);
  border-radius: 10px;
  box-shadow: 5px 5px 10px rgb(133, 133, 133);
  color: rgb(175, 175, 175);
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  height: 560px;
  margin: 5px;
  min-width: 350px;
  padding: 20px 10px;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.hist-body .list {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column-reverse;
          flex-direction: column-reverse;
  font-size: 20px;
  list-style: none;
  line-height: 40px;
  margin: 10px;
  padding: 10px;
  width: 100%;
}

.hist-body .list li:first-child {
  color: white;
  font-size: 30px;
  margin: 20px 0 0;
}

.hist-body h2 {
  color: white;
  font-weight: 100;
  text-shadow: black 1px 1px 5px;
}


